import {
	api
} from '../../js/api.js'

import qBar from '../../compontents/qBar.vue'
import qTab from '../../compontents/qTab.vue'
import comicdesc from './compontents/comicdesc.vue'

export default {
	data() {
		return {
			tabcurrent: 0,
			dotcolor: 'black',
			tablist: [{
				title: '详情'
			}, {
				title: '目录'
			}],
			barcolor: '#000',
			comicID: '',
			barHeight: 0,
			statusbarh:0,
			barImg: '',
			barTit: '漫画详情',
			comicdata: {},
		};
	},
	components: {
		qBar,
		qTab,
		comicdesc
	},
	onLoad(option) {
		let that = this;
		this.barHeight = uni.getStorageSync("SET_CUSTOM_BAR") + 150;
		this.statusbarh = uni.getStorageSync("SET_STATUS_BAR") + 5;
		console.log(this.statusbarh)
		this.comicID = option.id; //'107658'; //
		this.barImg = 'https://image.yqmh.com/mh/' + this.comicID + '.jpg';
		api.getDetails(this.comicID).then(comicInfo => {
			// console.log(JSON.stringify(comicInfo.cover_list[1]))
			that.comicdata = comicInfo;
			that.barImg = comicInfo.cover_list[1];
			that.barTit = comicInfo.comic_name;
		})
	},
	methods: {
		itemchange(index) {
			this.tabcurrent = index;
		},
		swiperchange(e) {
			// console.log(e)
			this.tabcurrent = e.detail.current;
		}
	}
}
